import 'babel-polyfill';
import React from 'react';
import { render } from 'react-dom';

// import { Admin, Resource, Delete } from 'admin-on-rest';
import Admin from './Admin';
import Resource from './Resource';
import Delete from './mui/delete/Delete';
import jsonRestClient from 'aor-json-rest-client';

import addUploadFeature from './addUploadFeature';
import Login from './mui/auth/Login';

import data from './data';
import authClient from './authClient';
import zhMessages from './i18n/messages';

const message = {
    zh:{...zhMessages}
}

const restClient = jsonRestClient(data, true);
const uploadCapableClient = addUploadFeature(restClient);
const delayedRestClient = (type, resource, params) => new Promise(resolve => setTimeout(() => resolve(uploadCapableClient(type, resource, params)), 1000));


render(
    <Admin authClient={authClient} restClient={delayedRestClient} title="LD300" loginPage={Login} locale="zh" messages={message}>
        <Resource name="tag" remove={Delete}/>
    </Admin>,
    document.getElementById('root'),
);


